#end{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15%;
}
#endBtn{
    width: 100px;
    height: 50px;
    margin-top: 20px;
    border-radius: 8px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
    border: none;
    color: aliceblue;
    background-color: #09a2f5;
}
#endBtn:hover{
    background-color: #0a7dc1;
}